import React, { useEffect, useLayoutEffect, useState } from 'react';
import { Link } from 'react-router-dom';

import Logo from '../assets/logo/ziptrak-logo.png';

const Navbar = React.lazy(() => import('../topbar/navbar/Navbar'));

const TopBar = () => {

    const [border, setBorder] = useState("1px solid yellow");
    const [navdisplay, setNavDisplay] = useState('none');

    const handlenavshow = () => {

        if (navdisplay === "block") {
            setNavDisplay("none");
        } else {
            setNavDisplay("block");
        }
    }

    useLayoutEffect(() => {
        if (window.innerWidth < 850) {
            setBorder("none");
            setNavDisplay("none");
        }
        else {

        }
    }, [])


    return (

        <div>

            <div className="topbar py-3">

                <div className="d-flex justify-content-between px-md-5 px-2">

                    <Link className="navbar-brand" to="/">
                        <img src={Logo} width="150px" className='img-fluid topbar_logo' alt="Logo Not Available" />
                    </Link>

                    <>

                        <div className="d-flex align-items-center" >

                            <div className="nav-item me-3 px-2 py-1 rounded-pill" style={{ border: border }}>
                                <Link className="nav-link topbar_link" aria-current="page" to="/">
                                    <span className="me-2 topbarLinks_text">Design Your Blind</span>
                                    <i className="fa-solid fa-cube"></i>
                                </Link>
                            </div>

                            <div className="nav-item me-3">
                                <Link className="nav-link topbar_link" aria-current="page" to="/">
                                    <span className="me-2 topbarLinks_text">Find A Retailer</span>
                                    <i className="fa-solid fa-map-location-dot"></i>
                                </Link>
                            </div>

                            <div className="nav-item me-3">
                                <Link className="nav-link topbar_link" aria-current="page" to="/">
                                    <span className="me-2 topbarLinks_text">Search</span>
                                    <i className="fa-solid fa-search"></i>
                                </Link>
                            </div>

                            <div className="nav-item me-3">
                                <Link className="nav-link topbar_link" aria-current="page" to="/">
                                    <span className="me-2 topbarLinks_text">Trade Login</span>
                                    <i className="fa-regular fa-circle-user"></i>
                                </Link>
                            </div>

                            <div>
                                <button className='btn btn-light btn-sm' onClick={handlenavshow}>
                                    <i className="fa-solid fa-sliders text-success"></i>
                                </button>
                            </div>

                        </div>
                    </>
                </div>
            </div>

            <div className={`d-${navdisplay} `}>
                <Navbar />
            </div>

        </div>
    )
}

export default TopBar;
